
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import HlCell from '../../base/hl-cell/hl-cell'
import { isWeiXin } from '../../util/util'
import { mapMutations } from 'vuex'
import Loading from '../../base/loading/loading'
import wx from 'weixin-js-sdk'
import api from '../../fetch/api'











export default {
props: {
itemList: {
type: Array,
default: ''
},
isLoading: {
type: Boolean,
default: false
}
},
data () {
return {
shopName: '',
pagState: '',
type: '1',
list: [{
src: 'http://placeholder.qiniudn.com/60x60/3cc51f/ffffff',
title: '',
desc: '',
url: '/component/cell'
}],
orderType: 9,
titleNo: '~'
}
},
created () {
this.signApi()
},
methods: {
/* @vOrderType 1. 2. 3.  */
toDetail (vGroupOrderGuid, vOrderGuid, vOrderType, vOrderState) {
if (vOrderType === 2 && vOrderState === 9) {
this.$router.push({path: '/groupdetail', query: {vGroupOrderGuid: vGroupOrderGuid}})
} else if (vOrderType === 2 && vOrderState === 2) {
this.$router.push({path: '/groupSuccess', query: {vGroupOrderGuid: vGroupOrderGuid, vOrderGuid: vOrderGuid}})
} else if (vOrderState === 1 || vOrderState === 2 || vOrderState === 3 || vOrderState === 5 || vOrderState === 6 || vOrderState === 7 || vOrderState === 8) {
this.$router.push({path: '/orderdetail', query: {vOrderGuid: vOrderGuid}})
}
},
cancelOder (vOrderGuidVal) {

let than = this
this.$confirm('', '?', function () {
than.getCancelOder(vOrderGuidVal)
})
},
getCancelOder (vOrderGuidVal) {

let that = this
api.cancelOrder({

vOrderGuid: vOrderGuidVal
}).then(res => {
if (res.code === 0) {

this.$msgbox('', '', function () {
that.$emit('cancelOrder')
})
}
}, err => {
this.$msgbox('', err.data.message)
})
},
doPay (orderGuid) {
let that = this
isWeiXin(function () {
that.toPay(orderGuid)
}, function () {
let options2 = {
'orderType': that.orderType,
'payway': 2,
'orderGuid': orderGuid
}
let jsonStr = JSON.stringify(options2)
that.anzhuoPay(jsonStr)
})
},
toPay (orderGuid) {

api.doPay({

orderGuid: orderGuid,
orderType: this.orderType
}).then(res => {
console.log('')
console.log(res)
let options = res.data
this.wxPay(options)
}, err => {
console.log('')
this.$msgbox('', err.data.message)
console.log(err)
})
},
/* ios */
anzhuoPay (jsonStr) {

this.$msgbox('', '')

},








wxPay (option) {
let that = this
wx.chooseWXPay({
'appId': option.appid,
'paySign': option.sign,
'timestamp': option.timestamp,
'nonceStr': option.noncestr,
'package': option.packageValue,
'signType': option.signType,
'success': function (res) {
console.log('88')
console.log(res)
if (res.errMsg === 'chooseWXPay:ok') {

that.$emit('cancelOrder')
} else if (res.errMsg === 'chooseWXPay:cancel') {
alert('')
} else if (res.errMsg === 'chooseWXPay:fail') {
alert('')
}
}
})
},

signApi () {
let urlRe = encodeURIComponent(window.location.href)
api.sign({
url: urlRe
}).then(res => {
console.log('res')
console.log(res)
let config = res.data
wx.config({
debug: false,
appId: config.appId,                // 
timestamp: config.timestamp,        // 
nonceStr: config.nonceStr,          // 
signature: config.signature,        // 1
jsApiList: config.jsApiList                // JSJS2
})
let options = {
title: '',
desc: '',
link: '',
imgUrl: '',
success: function () {
},
cancel: function () {
}
}
wx.ready(function (res) {
wx.onMenuShareTimeline(options)
wx.onMenuShareAppMessage(options)
})
}, err => {
console.log('err')
console.log(err)
})
},
isPagState (pagState, invert) {
if (pagState === 1) {
return ''
} else if (pagState === 2) {
if (invert === 2) {
return ''
} else {
return ''
}
} else if (pagState === 3) {
return ''
} else if (pagState === 5) {
return ''
} else if (pagState === 6) {
return ''
} else if (pagState === 7) {
return ''
} else if (pagState === 8) {
return '()'
} else if (pagState === 9) {
return ''
}
},
isQuantity (quantity) {
return '' + quantity + ''
},
isTotalMoney (totalMoney) {
return '' + totalMoney + ''
},
...mapMutations({
set_vOrderGuid: 'SET_VORDERGUID'
})

},
components: {
Group,
Cell,
Panel,
HlCell,
XButton,
Loading
}
}



